<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleSystemconfigAdd"></el-button>
            <el-button class="btn" @click="handleBatchDelete"></el-button>
            <el-button class="btn" @click="flushPropsl"></el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="" icon="search" v-model="searchInput" :on-icon-click="handleSystemconfigSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="systemconfigData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="companyid" label="ID">
                <template slot-scope="scope">
                    <el-button @click="handlesystemconfigCheck(scope.$index, scope.row)" type="text">{{scope.row.companyid}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="confcode" label=""></el-table-column>
            <el-table-column prop="confname" label=""></el-table-column>
            <el-table-column prop="conftype" label=""></el-table-column>
            <el-table-column prop="confvalue" label=""></el-table-column>
            <el-table-column prop="status" label=""></el-table-column>
            <el-table-column prop="introduce" label=""></el-table-column>
            <el-table-column label="" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleSystemconfigDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click="handleSystemconfigEdit(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="systemconfigForm" :rules="formRules" labelPosition="left" ref="systemconfigForm">
                <el-form-item prop="companyid" label="ID" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.companyid" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="confcode" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.confcode" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="confname" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.confname" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="conftype" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.conftype" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="confvalue" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.confvalue" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="introduce" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.introduce" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="status" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigForm.status" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleSystemconfigSave('systemconfigForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="systemconfigEditForm" :rules="formRules" labelPosition="left" ref="systemconfigEditForm">
                <el-form-item prop="companyid" label="ID" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.companyid" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="confcode" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.confcode" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="confname" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.confname" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="conftype" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.conftype" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="confvalue" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.confvalue" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="introduce" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.introduce" auto-complete="off"></el-input>
                    
                </el-form-item>
                <el-form-item prop="status" label="" :label-width="formLabelWidth">
                    <el-input v-model="systemconfigEditForm.status" auto-complete="off"></el-input>
                    
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false"> </el-button>
            <el-button type="primary" @click="handleSystemconfigSave('systemconfigEditForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">ID: {{showCheck.companyid}}</div>
            <div class="item">: {{showCheck.confcode}}</div>
            <div class="item">: {{showCheck.confname}}</div>
            <div class="item">: {{showCheck.conftype}}</div>
            <div class="item">: {{showCheck.confvalue}}</div>
            <div class="item">: {{showCheck.createAt}}</div>
            <div class="item">: {{showCheck.introduce}}</div>
            <div class="item">: {{showCheck.status}}</div>
            <div class="item">: {{showCheck.updateAt}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false"> </el-button>
            <el-button type="primary" @click="dialogFormCheck=false"> </el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    systemconfigList,
    savesystemconfig,
    deletesystemconfigByIds,
    flushProps,
  }from '@/api/getData';
    
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }else{
                 callback();
            }
          
        };
      return {
        //
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //
        systemconfigData: [],
        showCheck: {}, //
        dialogFormCheck: false, //

        multipleSelection: [],
        //
        dialogFormVisible: false,
        dialogFormEdit: false, //
        systemconfigForm: {
          companyid: '',
          confcode: '',
          confname: '',
          conftype: '',
          confvalue: '',
          introduce: '',
          status: '',
        },
        systemconfigEditForm: {
             companyid: '',
             confcode: '',
             confname: '',
             conftype: '',
             confvalue: '',
             introduce: '',
             status: '',
        },
        formLabelWidth: '80px',
        formRules: {
         
        },
      }
    },
    created() {
      this.getsystemconfigList(); //
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      // 
      async getsystemconfigList() {
        try {
          let query = {
            confcode: this.searchInput
          }
          //
          const res = await systemconfigList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.systemconfigData = [];
            // 
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                companyid:item.companyid,
                confcode:item.confcode,
                confname:item.confname,
                conftype:item.conftype,
                confvalue:item.confvalue,
                createAt:item.createAt,
                introduce:item.introduce,
                status:item.status,
                updateAt:item.updateAt,
                uuid:item.uuid,
              }
              this.systemconfigData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('', err);
        }
      },
      handSaveParam(args){
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getsystemconfigList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleSystemconfigSearch() {
        this.getsystemconfigList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //
      handleSystemconfigAdd() {
        this.systemconfigForm = {
          companyid: '',
          confcode: '',
          confname: '',
          conftype: '',
          confvalue: '',
          introduce: '',
          status: '',
        };
        this.dialogFormVisible = true;
      },
      //
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //
              const result = await deletesystemconfigByIds(data);
              if (result.resultCode == '0') {
                that.getsystemconfigList();
                this.$message({
                  type: 'success',
                  message: '!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //
      handleSystemconfigDelete(index, row) {
        let that = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //
            const result = await deletesystemconfigByIds(data);
            if (result.resultCode == '0') {
              that.getsystemconfigList();
              this.$message({
                type: 'success',
                message: '!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      handleSystemconfigEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.systemconfigEditForm.companyid = row.companyid;
            this.systemconfigEditForm.confcode = row.confcode;
            this.systemconfigEditForm.confname = row.confname;
            this.systemconfigEditForm.conftype = row.conftype;
            this.systemconfigEditForm.confvalue = row.confvalue;
            this.systemconfigEditForm.introduce = row.introduce;
            this.systemconfigEditForm.status = row.status;
            this.systemconfigEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //
      handlesystemconfigCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
      },
      //  
      handleSystemconfigSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await savesystemconfig(params);
              if (result.resultCode == '0') {
                  if(formName == 'systemconfigForm'){//
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getsystemconfigList();
                  this.$message({
                     type: 'success',
                     message: ''
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: ''
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: ''
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: ''
             });
            return false;
          }
        });
      },
      async flushPropsl() {
        try {
        //
        const res = await flushProps();
        if (res.resultCode == '0') {
            this.$message({
                type: 'info',
                message: ''
            });
        } else {
            throw new Error(res.message)
        }
        } catch (err) {
            console.log('', err);
        }
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



