<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleHolidayAdd"></el-button>
            <el-button class="btn" @click="handleBatchDelete"></el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="" :maxlength="10" icon="search" v-model="searchInput" :on-icon-click="handleHolidaySearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="holidayData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="year" label="">
                <template slot-scope="scope">
                    <el-button @click="handleholidayCheck(scope.$index, scope.row)" type="text">{{scope.row.year}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="date" label=""></el-table-column>
            <el-table-column label="" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleHolidayDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click="handleHolidayEdit(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="holidayForm" :rules="formRules" labelPosition="left" ref="holidayForm">
                <el-form-item prop="year" label="" :label-width="formLabelWidth">
                    <el-select v-model="holidayForm.year" placeholder="">
                        <el-option  v-for="item in yearArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="name" label="" :label-width="formLabelWidth">
                    <el-select v-model="holidayForm.name" placeholder="">
                        <el-option  v-for="item in holidayday" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="date" label="" :label-width="formLabelWidth">
                    <el-date-picker
                    v-model="dayArray"
                    :editable="editable"
                    format="yyyy  MM  dd "
                    value-format="yyyy-MM-dd"
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleHolidaySave('holidayForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="holidayEditForm" :rules="formRules" labelPosition="left" ref="holidayEditForm">
                <el-form-item prop="year" label="" :label-width="formLabelWidth">
                    <el-select v-model="holidayEditForm.year" placeholder="">
                        <el-option  v-for="item in yearArr" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="name" label="" :label-width="formLabelWidth">
                    <el-select v-model="holidayEditForm.name" placeholder="">
                        <el-option  v-for="item in holidayday" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="date" label="" :label-width="formLabelWidth">
                    <el-date-picker
                    v-model="dayArray"
                    type="daterange"
                    :editable="editable"
                    value-format="yyyy-MM-dd"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false"> </el-button>
            <el-button type="primary" @click="handleHolidaySave('holidayEditForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">: {{showCheck.createAt}}</div>
            <div class="item">: {{showCheck.date}}</div>
            <div class="item">: {{showCheck.name}}</div>
            <div class="item">: {{showCheck.updateAt}}</div>
            <div class="item">: {{showCheck.year}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false"> </el-button>
            <el-button type="primary" @click="dialogFormCheck=false"> </el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    holidayList,
    saveholiday,
    deleteholidayByIds
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }else{
                 callback();
            }
          
        };
      return {
        //
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //
        holidayData: [],
        showCheck: {}, //
        dialogFormCheck: false, //

        multipleSelection: [],
        //
        dialogFormVisible: false,
        dialogFormEdit: false, //
        holidayForm: {
          year: '',
          name: '',
          date: '',
        },
        holidayEditForm: {
             year: '',
             name: '',
             date: '',
        },
        formLabelWidth: '80px',
        formRules: {},
        yearArr: [],
        toyear:'',
        holidayday: [{
          value: '',
          label: ''
        },{
          value: '',
          label: ''
        },{
          value: '',
          label: ''
        },{
          value: '',
          label: ''
        },{
          value: '',
          label: ''
        },{
          value: '',
          label: ''
        },{
          value: '',
          label: ''
        },{
          value: '',
          label: ''
        }],
       
        editable: false,
        dayArray: [],
      }
    },
    created() {
        this.getholidayList(); //
        this.initYear();
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      // 
      async getholidayList() {
        try {
          let query = {
            name: this.searchInput
          }
          //
          const res = await holidayList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.holidayData = [];
            // 
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                createAt:item.createAt,
                date:item.date,
                name:item.name,
                updateAt:item.updateAt,
                uuid:item.uuid,
                year:item.year,
              }
              this.holidayData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('', err);
        }
      },
      handSaveParam(args){
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getholidayList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleHolidaySearch() {
        this.getholidayList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //
      handleHolidayAdd() {
        this.holidayForm = {
          year: this.$data.toyear,
          name: '',
          date: '',
        };
        this.$data.dayArray = [];
        this.$data.dayArray.push(this.$moment(new Date()).format('YYYY-MM-DD'));
        this.$data.dayArray.push(this.$moment(new Date()).format('YYYY-MM-DD'));
        this.dialogFormVisible = true;
      },
      //
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //
              const result = await deleteholidayByIds(data);
              if (result.resultCode == '0') {
                that.getholidayList();
                this.$message({
                  type: 'success',
                  message: '!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //
      handleHolidayDelete(index, row) {
        let that = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //
            const result = await deleteholidayByIds(data);
            if (result.resultCode == '0') {
              that.getholidayList();
              this.$message({
                type: 'success',
                message: '!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      handleHolidayEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.holidayEditForm.year = row.year;
            this.holidayEditForm.name = row.name;
            this.holidayEditForm.date = row.date;
            this.holidayEditForm.uuid = row.uuid;
            var dates = row.date;
            this.$data.dayArray = dates.split("");
        }
        this.dialogFormEdit = true;
      },
      //
      handleholidayCheck(index, row) {
        this.dialogFormCheck = true;
        this.$data.showCheck = row;
      },
      //  
      handleHolidaySave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //
              var args = this.$refs[formName].model;
              
              args['date'] =this.$moment(this.$data.dayArray[0]).format('YYYY-MM-DD')+""+this.$moment(this.$data.dayArray[1]).format('YYYY-MM-DD');
              var params = this.handSaveParam(args);
              const result = await saveholiday(params);
              if (result.resultCode == '0') {
                  if(formName == 'holidayForm'){//
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getholidayList();
                  this.$message({
                     type: 'success',
                     message: ''
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: ''
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: ''
               });
            }
          } else {
             this.$message({
                type: 'info',
                message: ''
             });
            return false;
          }
        });
      },
      initYear(){
        var today = new Date();
        var year = today.getFullYear();
        var nextYear = year + 1;
        var toyear = {};
        toyear['label'] = year;
        toyear['value'] = year; 
        var next = {};
        next['label'] = nextYear;
        next['value'] = nextYear; 
        this.$data.yearArr.push(toyear);
        this.$data.yearArr.push(next);
        this.$data.toyear = year;
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



