<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="float-l btn" @click="handleUserAdd"></el-button>
            <el-button class="btn" @click="handleBatchDelete"></el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="" :maxlength="32" icon="search" v-model="searchInput" :on-icon-click="handleUserSearch">
             </el-input>
            </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="userData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="">
                <template slot-scope="scope">
                    <el-button @click="handleuserCheck(scope.$index, scope.row)" type="text">{{scope.row.vlanId}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="username" label=""></el-table-column>
            <el-table-column label="" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleUserDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" size="small" @click="handleUserEdit(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormVisible" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="userForm" :rules="formRules" labelPosition="left" ref="userForm">
                <el-form-item prop="username" label="" :label-width="formLabelWidth">
                    <el-input v-model="userForm.username" :maxlength="32" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="password" label="" :label-width="formLabelWidth">
                    <el-input v-model="userForm.password" :maxlength="32" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth">
                    <el-select v-model.number="userForm.vlanId" placeholder="">
                        <el-option  v-for="item in companyInfo" :key="item.value" :label="item.value" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="role" label=""  :label-width="formLabelWidth">
                    <el-select v-model="userForm.role" placeholder="">
                        <el-option  v-for="item in roleInfo" :key="item.value" :label="item.label" :value="item.label">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="handleUserSave('userForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormEdit" @close="handleDialogWithFormClose()" :close-on-click-modal="2==1">
            <el-form :model="userEditForm" :rules="formRules" labelPosition="left" ref="userEditForm">
                <el-form-item prop="username" label="" :label-width="formLabelWidth">
                    <el-input v-model="userEditForm.username" :maxlength="32" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="vlanId" label="" :label-width="formLabelWidth">
                    <el-select v-model.number="userEditForm.vlanId" placeholder="">
                        <el-option  v-for="item in companyInfo" :key="item.value" :label="item.value" :value="item.value">
                        </el-option>
                    </el-select> 
                </el-form-item>
                <el-form-item prop="role" label="" :label-width="formLabelWidth">
                    <el-select v-model="userEditForm.role" placeholder="">
                        <el-option  v-for="item in roleInfo" :key="item.value" :label="item.label" :value="item.label">
                        </el-option>
                    </el-select> 
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormEdit = false"> </el-button>
            <el-button type="primary" @click="handleUserSave('userEditForm')"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-dialog title="" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
            <div class="item">: {{showCheck.username}}</div>
            <div class="item">: {{showCheck.vlanId}}</div>
            <div class="item">: {{showCheck.createAt}}</div>
            <div class="item">: {{showCheck.updateAt}}</div>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormCheck = false"> </el-button>
              <el-button type="primary" @click="dialogFormCheck=false"> </el-button>
            </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    userList,
    saveuser,
    deleteuserByIds,
    selectAllRole,
    selectAllCompany
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  import moment from 'moment';
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //
            if (value == undefined || "" === value) {
                callback(new Error(''));
            }else{
                 callback();
            }
          
        };
      return {
        //
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //
        userData: [],
        showCheck: {}, //
        dialogFormCheck: false, //
        roleInfo: [],//
        companyInfo: [],//
        multipleSelection: [],
        //
        dialogFormVisible: false,
        dialogFormEdit: false, //
        userForm: {
          vlanId: '',
          username: '',
          role: '',
          password: '',
        },
        userEditForm: {
             vlanId: '',
             username: '',
             role: '',
        },
        formLabelWidth: '80px',
        formRules: {
         
        },
      }
    },
    created() {
      this.getuserList(); //
      this.selectAllRole();
      this.selectAllCompany()
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      // 
      async getuserList() {
        try {
          let query = {
            username: this.searchInput
          }
          //
          const res = await userList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.userData = [];
            // 
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              const tableItem = {
                createAt:item.createAt,
                vlanId:item.vlanId,
                updateAt:item.updateAt,
                username:item.username,
                role:item.roles,
                uuid:item.uuid,
              }
              this.userData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('', err);
        }
      },
      async selectAllRole() {
        try {
            const res = await selectAllRole();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   if(value.name === '')continue;
                   if(this.$store.baseStore.getters.getUserInfo.managerLevel!='999' && value.name === '') continue;
                   temp.label = value.name;
                   temp.value = value.uuid;
                   this.$data.roleInfo.push(temp);
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('', err);
        }
      },
      async selectAllCompany() {
        try {
            const res = await selectAllCompany();
            if (res.resultCode == '0') {
                for (var value of res.result){
                   var temp = {};
                   temp.value = value.companyid;
                   this.$data.companyInfo.push(temp);
                }
            }else {
               throw new Error(res.result)
            }
        } catch (err) {
            console.log('', err);
        }
      },
      handSaveParam(args){
         return args;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getuserList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleUserSearch() {
        this.getuserList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //
      handleUserAdd() {
        this.userForm = {
          role: '',
          vlanId: '',
          username: '',
          password: '',
        };
        this.dialogFormVisible = true;
      },
      //
      handleDialogWithFormClose() {
        for (var prop in this.$refs) {
          if (this.$refs.hasOwnProperty(prop)) {
            if (this.$refs[prop].$vnode.tag == 'vue-component-48-ElForm') {
              this.$refs[prop].resetFields();
            }
          }
        }
      },
      //
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //
              const result = await deleteuserByIds(data);
              if (result.resultCode == '0') {
                that.getuserList();
                this.$message({
                  type: 'success',
                  message: '!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }
      },
      //
      handleUserDelete(index, row) {
        let that = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //
            const result = await deleteuserByIds(data);
            if (result.resultCode == '0') {
              that.getuserList();
              this.$message({
                type: 'success',
                message: '!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      handleUserEdit(index, row) {
        if (typeof row !== 'undefined') {
            this.userEditForm.vlanId = row.vlanId;
            this.userEditForm.username = row.username;
            this.userEditForm.role = row.role[0].name;
            this.userEditForm.uuid = row.uuid;
        }
        this.dialogFormEdit = true;
      },
      //
      handleuserCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
      },
      //  
      handleUserSave(formName) {
        let that = this;
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            try {
              //
              var args = this.$refs[formName].model;
              var params = this.handSaveParam(args);
              const result = await saveuser(params);
              if (result.resultCode == '0') {
                  if(formName == 'userForm'){//
                        that.dialogFormVisible = false;
                  }else{
                    this.dialogFormEdit = false;
                  }
                  that.getuserList();
                  this.$message({
                     type: 'success',
                     message: ''
                  });
              }else{
                  this.$message({
                    type: 'error',
                    message: ''
                  });
              }
            } catch (err) {
               this.$message({
                  type: 'error',
                  message: ''
               });
            }
          } else {
             this.$message({
                type: ' info',
                message: ''
             });
            return false;
          }
        });
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



